import { _decorator, Component, Node, Prefab, Vec3, instantiate, macro } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameCtrl')
export class GameCtrl extends Component {
    @property(Prefab)
    public enemyWave1: Prefab [] = [];
    @property(Prefab)
    public enemyWave2: Prefab [] = [];
    @property(Prefab)
    public enemyWave3: Prefab [] = [];
    public spawnFunc1 = [false, false, false, false, false, false, false, false, false, false];

    start() {
        this.spawn(0);
    }

    update(deltaTime: number) {
        
    }

    spawn(i: number){
        if (i < this.enemyWave1.length) {
            setTimeout(() => {
                this.spawn(i + 1);
            }, 7000);
            for(let j = 0; j < 1; j++){
                let enemyPre = this.enemyWave1[i];
                let prefabInstance = instantiate(enemyPre);
                prefabInstance.parent = this.node.parent;

                if(enemyPre.name.endsWith('_0')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x + (120 * (j - 2)), this.node.worldPosition.y, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_1')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x - (160 * (j + 2)), this.node.worldPosition.y + 20, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_2')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 40, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_3')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 60, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_4')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 80, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_5')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 100, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_6')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 120, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_7')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 140, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_8')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 160, this.node.worldPosition.z);
                }
                if(enemyPre.name.endsWith('_9')){
                    prefabInstance.setWorldPosition(this.node.worldPosition.x, this.node.worldPosition.y + 180, this.node.worldPosition.z);
                }
                
            }
        }else{return;}
            

    }
}


